<template>
    <div class="page-container">
        <div id="webgl-output"></div>
    </div>
</template>

<script>
import * as THREE from "three";
import * as dat from "dat.gui";
import * as Stats from "stats.js";

import createGeometry from "./utils/createGeometry";
export default {
    components: {},
    props: {},
    data() {
        return {
            scene: null,
            camera: null,
            gui: null,
            renderer: null,
            plane: null,
            cube: null,
            sphere: null,
            sphere2: null,
            sphere3: null,
            guiConfiguration: {
                message: "ÂìàÂñΩÂïä~ÊàëÊòØËç£È°∂",
                cubeSpeed: 0.03,
                sphereInitVelocity: 0.03,
                sphereAcceleration: 0.04,
                checkBox: false,
                button() {
                    alert("ÂÖ≥Ê≥®ÂÖ¨‰ºóÂè∑ÔºöÂâçÁ´ØË∂Ö‰∫∫");
                },
                sphere3Color: "#ffae23",
            },
        };
    },
    computed: {},
    watch: {},
    created() {},
    mounted() {
        this.init();
        this.animate();
        this.configGUI();
    },
    beforeRouteLeave(to, from, next) {
        console.log(123);
        this.gui.domElement.style.display = "none";
        next();
    },

    methods: {
        init() {
            //ÂÆö‰πâÂú∫ÊôØ
            this.scene = new THREE.Scene();
            //ÁªôÂú∫ÊôØÊ∑ªÂä†ÈõæÂåñÊïàÊûú
            // this.scene.fog = new THREE.Fog(0x123, 5, 10);
            // this.scene.fog = new THREE.FogExp2(0xffffff, 0.004);
            //ÂÆö‰πâÊëÑÂÉèÊú∫
            this.camera = new THREE.PerspectiveCamera(
                45,
                window.innerWidth / (window.innerHeight - 60),
                0.1,
                1000,
            );

            //ÂàõÂª∫ÂùêÊ†áÁ≥ª
            this.createAxes();
            //ÂàõÂª∫Âπ≥Èù¢
            this.createPlane();
            //ÂàõÂª∫ÂÖâÊ∫ê
            this.createLight();
            //ÂàõÂª∫Ê∏≤ÊüìÂô®,ÊîæÊúÄÂêé
            this.createRenderer();
            this.getStats();
            //ÂàõÂª∫Á´ãÊñπ‰Ωì
            this.cube = createGeometry(
                this.scene,
                {
                    type: "BoxGeometry",
                    attribute: [5, 5, 5],
                },
                { type: "MeshLambertMaterial", options: { color: 0xff0000 } },
                {
                    position: [0, 4, 0],
                    castShadow: true,
                },
            );
            this.cube.name = "cube";
            //ÂàõÂª∫ÁêÉ‰Ωì
            this.sphere = createGeometry(
                this.scene,
                {
                    type: "SphereGeometry",
                    attribute: [3, 20, 20],
                },
                { type: "MeshLambertMaterial", options: { color: 0x7777ff } },
                {
                    position: [-15, 3, 10],
                    castShadow: true,
                },
            );
            this.sphere2 = createGeometry(
                this.scene,
                {
                    type: "SphereGeometry",
                    attribute: [1, 20, 20],
                },
                { type: "MeshLambertMaterial", options: { color: "lightgreen" } },
                {
                    position: [20, 1, 0],
                    castShadow: true,
                },
            );
            this.createSphere3();
        },
        
        createSphere3() {
            this.sphere3 = createGeometry(
                this.scene,
                {
                    type: "SphereGeometry",
                    attribute: [8, 20, 20],
                },
                {
                    type: "MeshLambertMaterial",
                    options: { color: this.guiConfiguration.sphere3Color },
                },
                {
                    position: [40, 8, 0],
                    castShadow: true,
                },
            );
        },
        // ÂàõÂª∫Ê∏≤ÊüìÂô®
        createRenderer() {
            this.renderer = new THREE.WebGLRenderer();
            //ËÆæÁΩÆÂú∫ÊôØÁöÑËÉåÊôØÈ¢úËâ≤
            this.renderer.setClearColor(new THREE.Color(0x000000));
            //ËÆæÁΩÆÂú∫ÊôØÂ§ßÂ∞è
            this.renderer.setSize(window.innerWidth, window.innerHeight - 60);
            //ËÆæÁΩÆÁõ∏Êú∫‰ΩçÁΩÆ(x,y,z)
            this.camera.position.set(-100, 40, 100);
            // ÈÄöËøálookAtÂ∞ÜÊëÑÂÉèÊú∫ÊåáÂêëÂú∫ÊôØ‰∏≠ÂøÉ,(ÈªòËÆ§ÊåáÂêë0,0,0)
            this.camera.lookAt(this.scene.position);
            //ÂºÄÂêØÈò¥ÂΩ±
            this.renderer.shadowMap.enabled = true;
            // Â∞ÜÊ∏≤ÊüìÁªìÊûúÊ∑ªÂä†Âà∞domÂÖÉÁ¥†‰∏≠
            document.getElementById("webgl-output").appendChild(this.renderer.domElement);
            //‰ΩøÁî®ÊåáÂÆöÁöÑÊëÑÂÉèÊú∫Êù•Ê∏≤ÊüìÂú∫ÊôØ
            this.renderer.render(this.scene, this.camera);
        },
        //ÂàõÂª∫ÂÖâÊ∫ê
        createLight() {
            /* ÈúÄË¶ÅÊ≥®ÊÑèÁöÑÊòØÔºöMeshBasicMaterialÊùêË¥®‰∏ç‰ºöÂØπÂÖâÊ∫êÊúâ‰ªª‰ΩïÂèçÂ∫îÔºåÂü∫Êú¨ÊùêË¥®Âè™‰ºö‰ΩøÁî®ÊåáÂÆöÁöÑÈ¢úËâ≤Êù•Ê∏≤ÊüìÁâ©‰Ωì */
            //ÂÆö‰πâÂÖâÊ∫ê
            this.spotLight = new THREE.SpotLight(0xffffff);
            //ËÆæÁΩÆÂÖâÊ∫ê‰ΩçÁΩÆ
            this.spotLight.position.set(70, 130, 70);
            // ÂêØÁî®Èò¥ÂΩ±ÂäüËÉΩ
            this.spotLight.castShadow = true;
            //Â∞ÜÂÖâÊ∫êÊ∑ªÂä†ËøõÂú∫ÊôØ
            this.scene.add(this.spotLight);
        },
        // ÂàõÂª∫ÂùêÊ†áÁ≥ª
        createAxes() {
            //ÂàõÂª∫ÂùêÊ†áÁ≥ª,ËÆæÁΩÆËΩ¥Á∫øÁ≤óÁªÜÂÄº‰∏∫20
            this.axes = new THREE.AxesHelper(20);
            //Â∞ÜËΩ¥Á∫øÊ∑ªÂä†Âà∞Âú∫ÊôØ‰∏≠
            this.scene.add(this.axes);
        },
        //ÂàõÂª∫Âπ≥Èù¢
        createPlane() {
            //ÂÆö‰πâÂπ≥Èù¢ÁöÑÂ§ßÂ∞è
            let planeGeometry = new THREE.PlaneGeometry(200, 200);
            // ÈÄöËøáÂàõÂª∫ÊùêË¥®ÂØπË±°Êù•ËÆæÁΩÆÂπ≥Èù¢ÁöÑÂ§ñËßÇ,ËøôÈáå‰ΩøÁî®ÁöÑÊòØÂü∫Êú¨ÊùêË¥®
            let planeMaterial = new THREE.MeshLambertMaterial({
                color: 0xaaaaaa,
            });
            //Â∞ÜÂ§ßÂ∞èÂíåÂ§ñËßÇÁªÑÂêàËøõMeshÂØπË±°,ËµãÂÄºÁªôÂπ≥Èù¢ÂØπË±°
            this.plane = new THREE.Mesh(planeGeometry, planeMaterial);
            //Âπ≥Èù¢ÁªïxËΩ¥ÊóãËΩ¨‰πùÂçÅÂ∫¶
            this.plane.rotation.x = -0.5 * Math.PI;
            // ÂÆö‰πâÂÖ∂Âú®Âú∫ÊôØ‰∏≠ÁöÑ‰ΩçÁΩÆ
            this.plane.position.set(0, 0, 0);
            //Êé•Êî∂ÂÖâÊ∫ê
            this.plane.receiveShadow = true;
            //Ê∑ªÂä†Âπ≥Èù¢Âà∞Âú∫ÊôØ‰∏≠
            this.scene.add(this.plane);
        },

        //ÊâßË°åÂä®Áîª
        animate() {
            // stats.update();
            this.cube.rotation.x += this.guiConfiguration.cubeSpeed;
            this.cube.rotation.y += this.guiConfiguration.cubeSpeed;
            this.cube.rotation.z += this.guiConfiguration.cubeSpeed;
            // this.sphere.position.x += this.guiConfiguration.speed;
            // if (this.sphere.position.x > 20) {
            //     this.sphere.position.x = -20;
            // }
            this.guiConfiguration.sphereInitVelocity += this.guiConfiguration.sphereAcceleration;
            this.sphere.position.x = 20 * Math.cos(this.guiConfiguration.sphereInitVelocity);
            this.sphere.position.z = 20 * Math.sin(this.guiConfiguration.sphereInitVelocity);

            this.sphere2.position.x = 10 * Math.cos(this.guiConfiguration.sphereInitVelocity + 0.9);
            this.sphere2.position.z = 10 * Math.sin(this.guiConfiguration.sphereInitVelocity + 0.9);

            this.sphere3.position.x = 40 * Math.cos(this.guiConfiguration.sphereInitVelocity - 0.9);
            this.sphere3.position.z = 40 * Math.sin(this.guiConfiguration.sphereInitVelocity - 0.9);
            this.renderer.render(this.scene, this.camera);
            requestAnimationFrame(this.animate);
        },
        //ÈÖçÁΩÆdat.gui
        configGUI() {
            // let options = {
            //     message: "ÂìàÂñΩÂïä~ÊàëÊòØËç£È°∂",
            //     speed: 0.8,
            //     checkBox: false,
            //     button: function () {
            //         alert(123);
            //     },
            // };

            this.gui = new dat.GUI();

            this.gui.add(this.guiConfiguration, "message");
            this.gui.add(this.guiConfiguration, "cubeSpeed", 0, 0.5);
            this.gui.add(this.guiConfiguration, "sphereInitVelocity", -1, 1);
            this.gui.add(this.guiConfiguration, "sphereAcceleration", 0, 1);
            this.gui.add(this.guiConfiguration, "checkBox");
            this.gui.add(this.guiConfiguration, "button").name("ÁÇπÊàë");

            // var testObj = {
            //     color0: "#ffae23", // CSS string
            //     color1: [0, 128, 255], // RGB array
            //     color2: [0, 128, 255, 0.3], // RGB with alpha
            //     color3: { h: 350, s: 0.9, v: 0.3 }, // Hue, saturation, value
            // };
            var f1 = this.gui.addFolder("ÁêÉÁöÑÈ¢úËâ≤");
            let controller = f1.addColor(this.guiConfiguration, "sphere3Color").name("CSSÈ¢úËâ≤ÂÄº");
            //Á¨¨‰∫å‰∏™ÂàÜÁªÑÈªòËÆ§ÊâìÂºÄ
            f1.open();
            // f1.addColor(testObj, "color0").name("CSSÈ¢úËâ≤ÂÄº");
            // f1.addColor(testObj, "color1").name("RGBÈ¢úËâ≤ÂÄº");
            // f1.addColor(testObj, "color2").name("RGBAÈ¢úËâ≤ÂÄº");
            // f1.addColor(testObj, "color3").name("HUBÈ¢úËâ≤ÂÄº");
            this.gui.domElement.style = "position:absolute;top:300px;right:0px";
            //ÂØπÂ∫îÊéßÂà∂È°πÂÄº‰øÆÊîπÂÆåÊØïÂìçÂ∫î
            controller.onFinishChange((val) => {
                console.log("üöÄüöÄüöÄ / val", val);
                // this.sphere3.color.set(val);
                this.scene.remove(this.sphere3);
                this.createSphere3();
            });
        },
        //Ëé∑ÂèñpfsÁä∂ÊÄÅ
        getStats() {
            var stats = new Stats();
            stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
            document.querySelector(".page-container").appendChild(stats.dom);
            stats.domElement.style = "position:absolute;bottom:0px;left:0px";
            function animate() {
                stats.begin();
                // monitored code goes here
                stats.end();
                requestAnimationFrame(animate);
            }
            requestAnimationFrame(animate);
        },
    },
};
</script>

<style scoped lang="scss">
.page-container {
    // position: relative;
}
</style>
